*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}
.free{
    padding: 10px 30px;
    width: 100%;
    background: #010f1c;
    display: flex;
}
.free .icon{
    color: #f3b123;
    font-size: 32px;
}
.free p {
    margin-left: 10px;
    color: #f6f7f9;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 600;
}
.main-header{
    padding: 30px 40px;
    width: 100%;
    border-bottom: 1px solid #010f1c;
}
/*.main-header .container{
    display: flex;
    justify-content: space-between;
}*/

.main-header .container .search-box input{
    
    padding: 10px 30px;
    width:100%;
    color: #010f1c;
}
.main-header .container .search-box button{
   margin-left: 2px;
    outline: none;
    padding: 12px 20px;
    cursor: pointer;
    
    color: #ecf2f7;
 
} 
.main-header .container .icon{
    display: flex;
    margin-right: 50px;

}
.main-header .container .icon .account{
    display: flex;
    margin-right: 30px;
}
.main-header .container .icon .account .user-icon{
    margin-right: 10px;
    font-size: 22px;
    color: #010f1c;
    cursor: pointer;
}
.main-header .container .icon .account p{
    margin-right: 5px;
    margin-top: 2px;
}
/*.main-header .container .icon .second-icon{
    display: flex;

}*/
.main-header .container .icon .second-icon .link{
    margin-right: 5px;
    font-size: 22px;
    cursor: pointer;
    color: #0989ff;
}
.header{
    width: 100%;
    padding: 20px 30px;
    box-shadow: rgb(0, 0, 0, 0.35) 0px 5px 15px;
}
.header .container{
    display: flex;
    max-width: 100%;
    justify-content: space-between;
}
.header .container .nav ul{
  display: flex;
}
.header .container .nav ul li{
    list-style-type: none;
    margin-left: 20px;
}
.header .container .nav ul li .link{
    text-decoration: none;
    color: #010f1c;
    transition: 0.5s;
}
.header .container .nav ul li .link:hover{
    color: #0989ff;

}
.header .container .auth{
    margin-right: 20px;
    font-size: 22px;
    cursor: pointer;
}
.header .container .auth button{
    margin-right: 20px;
    font-size: 22px;
    cursor: pointer;
    outline: none;
    border: none;
    background: none;

}